import React from 'react';
import { Input, Textarea, Form, Button } from '../styled';
import axios from 'axios';

class Queryform extends React.Component{
    state = {name: '', email: '', message: '', status: ''}
    sendMsg = async (event) => {
        event.preventDefault()
        let { name, email, message } = this.state
        if(!name || !email || !message) return { "status": "unsuccess", "state": "Empty Fields" }

        await axios({
            method: 'post',
            url: 'http://rohan-blog-network.herokuapp.com/message',
            data: {name, email, message}
        });
        console.log('Message sent')
        this.setState({name: '', email: '', message: '', status: 'Message Sent'})
        setTimeout(() => {
            this.setState({status: ''})
        },10000)
    }

    render(){
        return (
            <Form onSubmit={this.sendMsg}>
                <h4>{this.state.status}</h4>
                <Input 
                    value={this.state.name}
                    onChange={(event) => {this.setState({name: event.target.value})}} 
                    type="text"  
                    placeholder="Name"  
                    autoComplete="off"
                />
                <Input
                    value={this.state.email}
                    onChange={(event) => {this.setState({email: event.target.value})}}                    
                    type="text" 
                    placeholder="Email" 
                    autoComplete="off" 
                />
                <Textarea 
                    value={this.state.message}
                    onChange={(event) => {this.setState({message: event.target.value})}}                    
                    placeholder="Message" 
                />
                <Button type="submit">Submit</Button>
            </Form>
        )
    }
}

export default Queryform;