import React from 'react';
import Flippy, { FrontSide, BackSide } from 'react-flippy';
import { SVGImg } from '../../styled';

class Professional extends React.Component{

    render(){
        return (
            <div style={{padding: '10px'}}>
                <Flippy
                    flipOnHover={true}
                    flipOnClick={true}
                    flipDirection="horizontal"
                    ref={(r) => this.flippy = r}
                    style={{ width: '300px', height: '400px' }}
                >
                    <FrontSide
                        style={{
                            fontFamily:'sans-serif',
                            backgroundColor: 'rgba(255, 255, 255,0.1)',
                            borderRadius: '4px'
                        }}
                    >
                    {front()}
                    </FrontSide>
                    <BackSide
                        style={{ 
                            fontFamily:'sans-serif',
                            backgroundColor: 'rgba(255, 0, 0, 0.2)',
                            borderRadius: '4px'
                        }}
                    >
                    </BackSide>
                </Flippy>
            </div>
        )
    }
}

const front = () => {
    return (
        <div style={frontStyle} >
            <div>
                <span style={{ margin: '0', fontSize: '130%', opacity: '0.6' }}>Professional</span>
            </div>
            <div>
                <SVGImg src="./images/social-icon/portfolio.svg" alt="icon" />
            </div>
        </div>
    );
};

const frontStyle = { 
    minWidth: '100%', 
    minHeight: '100%', 
    color: '#000', 
    display: 'flex', 
    flexFlow: 'column wrap', 
    justifyContent: 'center'
};

export default Professional;