import React from 'react'
import { SkillDiv, SkillDivContainer, SVGImg } from '../styled';

const Skills = () => {
    return (
        <SkillDivContainer>
            <SkillDiv>
                <h1 style={{fontSize: '2em'}}>Technologies I know of</h1>
                <div style={{margin: '0 10px'}}>
                    <SVGImg src="./images/nodejs.svg" alt="linux-icon" />
                    <SVGImg src="./images/react.svg" alt="linux-icon" />
                    <SVGImg src="./images/redux.svg" alt="linux-icon" />
                    <SVGImg src="./images/mongodb.svg" alt="linux-icon" />
                    <SVGImg src="./images/javascript.svg" alt="linux-icon" />
                    <SVGImg src="./images/python.svg" alt="linux-icon" />
                    <SVGImg src="./images/git.svg" alt="linux-icon" />
                    <SVGImg src="./images/mysql.svg" alt="linux-icon" />
                    <SVGImg src="./images/ubuntu.svg" alt="ubuntu-icon" />
                    <SVGImg src="./images/linux.svg" alt="linux-icon" />
                    <SVGImg src="./images/rabbitmq.svg" alt="linux-icon" />
                    <SVGImg src="./images/vscode.svg" alt="linux-icon" />
                    <SVGImg src="./images/css3.svg" alt="linux-icon" />
                    <SVGImg src="./images/html5.svg" alt="linux-icon" />
                    <SVGImg src="./images/mocha.svg" alt="linux-icon" />
                </div>
            </SkillDiv>
        </SkillDivContainer>
    )
}

export default Skills
