import React from 'react';
import { ProfileCard } from '../styled';
import TextChange from './TextChange';


class Profile extends React.Component {

    render(){
        return (
            <ProfileCard>
                <div style={{flex: '0.7', margin: '0'}}>
                    <h1>
                        <span style={{textAlign: 'center', fontSize: '2em', fontWeight: 'bold'}}>Hello</span>
                    </h1>
                    <h1>My name is Rohan...</h1>
                    <div>
                        <h2>I am a
                            <p style={{display: 'inline'}}> </p>
                            <TextChange />
                        </h2>
                    </div>
                    <h2>seeking job oppurtunities</h2>
                </div>
                <div style={{flex: '0.2'}}>
                    <img src='logo192.png' alt="dp"/>
                </div>
            </ProfileCard>
        )
    }
}

export default Profile;