import React from 'react';
import Particles from "react-tsparticles";

class ReactParticles extends React.Component {
    render() {
        return (
            <Particles id="particles"
                options={{
                    background: { color: { value: "#000" } },
                    fpsLimit: 60,
                    interactivity: {
                        detectsOn: "canvas", events: { onClick: { enable: true, mode: "push" }, onHover: { enable: true, mode: "repulse" }, resize: true }, modes: {
                            bubble: {
                                distance: 400,
                                duration: 2,
                                opacity: 0.8,
                                size: 40,
                            },
                            push: {
                                quantity: 4,
                            },
                            repulse: {
                                distance: 100,
                                duration: 0.4,
                            },
                        },
                    },
                    particles: {
                        color: {
                            value: [
                                "#3998D0",
                                "#2EB6AF",
                                "#A9BD33",
                                "#FEC73B",
                                "#F89930",
                                "#F45623",
                                "#D62E32",
                                "#EB586E",
                                "#9952CF"
                              ],
                        },
                        collisions: {
                            enable: true,
                        },
                        move: {
                            direction: "none",
                            enable: true,
                            outMode: "bounce",
                            random: false,
                            speed: 2,
                            straight: false,
                        },
                        number: {
                            density: {
                                enable: true,
                                value_area: 800,
                            },
                            value: 80,
                        },
                        opacity: {
                            value: 0.7,
                        },
                        shape: {
                            type: "circle",
                        },
                        size: {
                            random: true,
                            value: 4,
                        },
                    },
                    detectRetina: true,
                }}
            />
        );
    }
}

export default ReactParticles;