import React from 'react'

const Contacts = () => {
    return (
        <div className="social">
            <a href="https://github.com/riskyMachine" style={{filter: 'invert(100%)', opacity: '0.6'}}>
                <img style={{height: '60px'}} src="./images/social-icon/github.png" alt="github"/>
            </a>
            <a href="https://www.linkedin.com/in/rohan-kanojia-77b55b15a/" style={{filter: 'invert(100%)', opacity: '0.6'}}>
                <img style={{height: '60px'}} src="./images/social-icon/linkedin.png" alt="github"/>
            </a>
        </div>
    )
}

export default Contacts
