import styled from 'styled-components';

export const ProfileCard = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 60px;
`

export const ContactForm = styled.form`
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
`

export const SkillDivContainer = styled.div`
    width: 100%;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 40px 0;
`

export const SkillDiv = styled.div`
    width: 65%; 
    color: #fff;
    text-align: center;
    margin: auto;
`

export const ExperienceContainer = styled.div`
    width: 100%;
    background-color: rgba(255, 255, 255, 0.2);
    padding: 40px 0;
`

export const ExperienceDiv = styled.div`
    width: 65%; 
    color: #fff;
    text-align: center;
    margin: auto;
`

export const SVGImg = styled.img`
    height: 60px;
    margin: 15px;
`

export const Form = styled.form`
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
`

export const Input = styled.input`
    width: 50%;
    margin: 10px 0;
    font-size: 1.5em;
    padding: 10px;
    background: none;
    outline: none;
    border: 1px solid #fff;
    color: #fff;
`

export const Textarea = styled.textarea`
    width: 50%;
    margin: 10px 0;
    font-size: 1.5em;
    padding: 10px;
    background: none;
    outline: none;
    border: 1px solid #fff;
    color: #fff;
`

export const Button = styled.button`
    padding: 15px 50px;
    margin: 10px;
    border: 2px solid #db7093;
    background: none;
    border-radius: 30px;
    cursor: pointer;
    color: #fff;
`