import React from 'react'
import { ExperienceContainer, ExperienceDiv } from '../styled';
import Professional from './Cards/Professional';
import Node from './Cards/Node';
import ReactExp from './Cards/ReactExp';

const Experience = () => {
    return (
        <ExperienceContainer>
            <ExperienceDiv>
                <h1 style={{fontSize: '2em'}}>Experience</h1>
                <div style={cardContainerStyle}>
                    <Professional />
                    <Node />
                    <ReactExp />
                </div>
            </ExperienceDiv>
        </ExperienceContainer>
    )
}

const cardContainerStyle = {
    margin: '0 10px', 
    display: 'flex', 
    flexDirection: 'row', 
    flexWrap: 'wrap', 
    justifyContent: 'space-around'
}

export default Experience;
